<template>
  <div>
    <div class="title">
      <span class="iconfont icon-left gobackIcon" @click="()=>{this.$router.go(-1)}"></span>
      <p>修改头像</p>
    </div>
    <div class="content">
      <div class="centerValue">
        <van-uploader class="avatorUpload" :after-read="afterRead" />
        <p>点击上传新的头像</p>
      </div>
      <div class="footBtn">
        <van-button block class="btn">确认修改</van-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data: () => ({
    userInfo: null
  }),
  created() {
    this.$axios.get(`ym/mall-online-merchant/info`).then(res => {
      if (res.code === 0) {
        this.userInfo = res.data;
        console.log(this.userInfo);
      } else {
        console.log("获取商家信息失败");
      }
    });
  },
  methods: {
    afterRead(e) {
      // 此时可以自行将文件上传至服务器
      let param = new FormData();
      let config = {
        headers: {
          Authorization:
            "bearer" + JSON.parse(this.$store.state.userInfo).access_token,
          "Content-Type": "form-data"
        }
      };
      param.append("files", e.file);
      this.$axios
        .post(`ym/oss-attachment/upload-image`, param, config)
        .then(res => {
          console.log(res);
          if (res.data.code === 0) {
            this.changeAvator(res.data.data[0].imgSrc);
          }
        });
    },
    changeAvator(newAvator) {
      let event = {
        mobile: this.userInfo.mobile,
        storeName: this.userInfo.storeName,
        description: this.userInfo.description,
        wechat: this.userInfo.wechat,
        storeLogo: newAvator,
        alipay: this.userInfo.alipay,
        idcardImgFront: this.userInfo.idcardImgFront,
        idcardImgBack: this.userInfo.idcardImgBack,
        businessLicenseNo: this.userInfo.businessLicenseNo,
        businessLicenseImg: this.userInfo.businessLicenseImg
      };
      console.log(event);
      this.$axios.post(`ym/mall-online-merchant/enter`, event).then(res => {
        console.log(res);
        if (res.data.code === 0) {
          this.$Toast("修改成功");
          this.$router.push('/shop');
        } else {
          this.$Toast("修改失败");
        }
      });
    }
  }
};
</script>
